.detail_wrapper {
  width: 900px;
  display: grid;
  grid-template-areas:
  'a a' 'b c';
  margin: auto;
}
.detail_wrapper h1 {
  text-align: center;
  grid-area: a;
}
.detail_content {
  font-size: 20px;
  grid-area: c;
  position: relative;
}
.detail_content p {
  font-size: 26px;
  font-weight: 700;
}
.detail_content p span {
  color: cadetblue;
}
button {
  position: absolute;
  bottom: 10px;
  width: 100px;
  height: 40px;
  background-color: #310909;
  border: none;
  border-radius: 10px;
  cursor: pointer;
  transition: all .4s;
}
button a {
  font-size: 20px;
  color: #fff;
}
button:hover {
  background-color: gray;
  color: #fff;
}